@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.homepage{width:100vw;height:100vh;color:white;padding:3em 5em;background-image:url(/acron/static/media/bg-pattern.6b542492.svg),linear-gradient(to top, #7028e4 0%, #e5b2ca 100%)}.homepage .hero-header{display:flex;justify-content:space-between}.homepage .hero-header nav{display:flex;justify-content:flex-end;align-items:center;width:100%}.homepage .hero-header nav a{display:block;color:inherit;text-decoration:none;font-size:18px;margin:0 1em}.homepage .hero-header nav a:hover{color:#4e5283;transition:.3s}.homepage .hero{width:100%;height:85vh;display:flex;flex-direction:column;justify-content:center;position:relative}.homepage .hero .text{font-size:48px;font-weight:900}.homepage .hero a{width:220px}.homepage .hero .btn{color:white;border:none;display:block;text-align:center;cursor:pointer;outline:none;overflow:hidden;position:relative;font-weight:700;font-size:15px;background-color:#4e5283;padding:17px 60px;margin:2em 0;border-radius:5px;box-shadow:0 5px 15px rgba(0,0,0,0.2)}.homepage .hero .btn span{position:relative;z-index:1}.homepage .hero .btn:after{content:"";position:absolute;left:0;top:0;height:490%;width:140%;background:#E5B2CA;transition:all .5s ease-in-out;transform:translateX(-98%) translateY(-25%) rotate(45deg)}.homepage .hero .btn:hover:after{transform:translateX(-9%) translateY(-25%) rotate(45deg)}@media only screen and (max-width: 768px){.homepage{padding:1em 2em}.homepage .hero-header{flex-direction:column}.homepage .hero-header .logo-container{display:flex;justify-content:center}.homepage .hero-header nav{margin-top:2em;justify-content:center}}

.header{width:100vw;height:15vh;display:flex;justify-content:space-between;align-items:center;padding:0 4em;background-color:white;background-size:cover;border-bottom:0.1rem solid #4E5283}.header .logo-container{width:20%;padding-left:20px}.header .logo-container .logo{width:80%;height:auto}.header .logo-container h2{color:#4E5283}.header nav{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end;margin-right:2em}.header nav .item{margin:20px;color:#4E5283}.header nav .item .app-btn{color:white;border:none;display:block;text-align:center;cursor:pointer;outline:none;overflow:hidden;position:relative;background-color:#E5B2CA;border-radius:5px;padding:5px 10px;font-weight:700;cursor:pointer}.header nav .item .app-btn:hover{opacity:70%;transition:.3s}.header nav .item:hover{color:#E5B2CA;transition:.3s}@media only screen and (max-width: 768px){.header{padding:0 0.5em}.header nav{margin-right:0}}

.about .container{width:100vw;height:85vh;display:flex}.about .container .left-half{flex:1 1;display:flex;justify-content:center;align-items:center;color:white;padding:0 3em;background-image:url(/acron/static/media/bg-pattern.6b542492.svg),linear-gradient(to top, #7028e4 0%, #e5b2ca 100%);background-size:cover;box-shadow:13px 18px 45px -18px rgba(0,0,0,0.75)}.about .container .left-half .text h1{font-size:36px;font-weight:900}.about .container .right-half{flex:1 1;display:flex;justify-content:center;align-items:center;padding:0 3em}.about .container .right-half p{color:#4E5283}.about .container .right-half a{color:#E5B2CA}@media only screen and (max-width: 768px){.about .container{flex-direction:column}}

.usage{width:100%}.usage .video-container{height:85vh;display:flex;justify-content:center;align-items:center}

thead{color:white}tbody{color:#4E5283;font-weight:bold}tfoot{color:#4E5283}table{width:100%;border-collapse:collapse}table,th,td{border:5px solid #4E5283}td,th{padding:5px;text-align:center}th{background-color:rgba(182,105,245,0.7)}td:nth-child(1){background-color:rgba(182,105,245,0.7);color:white;font-weight:bold}

.acron{width:100%;height:100vh;background-color:#F3ECFF}.acron .header{display:flex;justify-content:space-between;align-items:center;width:100%;height:15vh;padding:0 5em;margin:0;background-color:white;background-image:none}.acron .header .logo-container{flex:1 1;margin-top:.5em}.acron .header .logo-container .logo{width:15%;height:auto}.acron .header .clear-btn{background-color:transparent;color:#4E5283;border:none;display:block;text-align:center;cursor:pointer;outline:none;overflow:hidden;position:relative;font-weight:300;font-size:22px;margin:0 1em;padding:5px 10px;transition:all .2s}.acron .header .run-btn{background-color:#E5B2CA;color:white;border:none;display:block;text-align:center;cursor:pointer;outline:none;overflow:hidden;position:relative;border-radius:5px;font-weight:700;font-size:22px;padding:5px 10px;transition:all .2s ease-in-out}.acron .header .run-btn span{cursor:pointer;display:inline-block;position:relative;transition:.3s}.acron .header .run-btn span:after{font-family:'Font Awesome 5 Free';content:"\f04b";font-weight:900;position:absolute;text-align:center;font-size:20px;opacity:0;top:3px;right:-20px;transition:.3s}.acron .header .run-btn:hover span{padding-right:25px}.acron .header .run-btn:hover span:after{opacity:1;right:0}.acron .body{display:flex;color:#4E5283}.acron .body .left-half{flex:1 1;display:flex;flex-direction:column;background:none;height:85vh}.acron .body .left-half .inputs{flex:1 1;display:flex;flex-direction:column;padding:1em 2em;margin:2em;border-radius:15px;background-color:white;font-size:24px;box-shadow:0px 0px 40px -15px rgba(0,0,0,0.5)}.acron .body .left-half .inputs p{font-weight:700;margin:.5em 0}.acron .body .left-half .inputs p span{font-size:12px}.acron .body .left-half .inputs input{height:2.5em}.acron .body .left-half .inputs .add-disc{background-color:#D9BBF9;color:white;border:none;display:block;text-align:center;cursor:pointer;outline:none;overflow:hidden;position:relative;border-radius:5px;font-weight:700;font-size:18px;width:200px;padding:8px 10px;margin:1em 0 0 0;transition:all .2s ease-in-out}.acron .body .left-half .disciplines{flex:2 1;display:flex;flex-direction:column;overflow-y:scroll;background-color:white;margin:0 3em 2em 3em;border-radius:15px;box-shadow:0px 0px 40px -15px rgba(0,0,0,0.5)}.acron .body .left-half .disciplines .title{color:#4E5283;text-align:center;padding:.5em 0;border-bottom:0.1rem solid #4E5283}.acron .body .left-half .disciplines .disciplines-list{display:flex;flex-direction:column;overflow-x:hidden;margin:0em 1em}.acron .body .left-half .disciplines .disciplines-list .discipline-card{display:flex;justify-content:space-between;background-color:#D9BBF9;border-radius:10px;margin-top:.9em;padding:0.5em 1em}.acron .body .left-half .disciplines .disciplines-list .discipline-card h4{color:white;font-weight:700;font-size:24px}.acron .body .left-half .disciplines .disciplines-list .discipline-card p{color:white;padding-top:.3em}.acron .body .left-half .disciplines .disciplines-list .discipline-card button{color:white;background-color:#CCA7A2;border:none;display:block;text-align:center;cursor:pointer;outline:none;overflow:hidden;position:relative;border-radius:5px;font-size:18px;padding:5px 10px;transition:all .2s ease-in-out}.acron .body .left-half .disciplines .disciplines-list .discipline-card button i{display:flex;justify-content:center;align-items:center}.acron .body .right-half{height:85vh;flex:1 1;background:none;display:flex;flex-direction:column;margin-right:3em}.acron .body .right-half .chronograms{width:100%;height:85vh;margin:3em 2em 2em 0;overflow-x:hidden;overflow-y:scroll;background-color:white;border-radius:15px;box-shadow:0px 0px 40px -15px rgba(0,0,0,0.5)}.acron .body .right-half .chronograms .title{color:#4E5283;text-align:center;padding:.5em 0;border-bottom:0.1rem solid #4E5283}.acron .body .right-half .chronograms .tables{margin:1em .5em}.acron .clicked:active{transform:scale(0.8)}.tables .table-disc{color:white;background-color:#E5B2CA;border-radius:5px;font-weight:bold;margin:0 .5em;padding:.3em .5em}@media only screen and (max-width: 970px){.acron .body .left-half .inputs p{font-size:18px}.acron .body .left-half .inputs .add-disc{font-size:16px;width:180px}.acron .body .left-half .disciplines .title{font-size:14px}.acron .body .left-half .disciplines .disciplines-list .discipline-card h4{font-size:16px;margin-top:.3em}.acron .body .left-half .disciplines .disciplines-list .discipline-card p{font-size:12px;margin-top:.5em}.acron .body .right-half{margin-right:3em}.acron .body .right-half .chronograms .title{font-size:14px}}@media only screen and (max-width: 768px){.acron{overflow-y:scroll}.acron .header{padding:0 2em}.acron .header .logo-container{margin-top:0}.acron .header .clear-btn{font-size:12px}.acron .header .run-btn{font-size:12px}.acron .body{flex-direction:column}.acron .body .left-half .inputs p{font-size:18px}.acron .body .left-half .inputs .add-disc{font-size:16px;width:180px}.acron .body .left-half .disciplines p{font-size:12px}.acron .body .right-half{margin:0 3em}.acron .body .right-half .chronograms{overflow-x:scroll;margin-top:1em}}

* {
  box-sizing: border-box;
  margin: 0;
